define('frontend/controllers/sign-in', ['exports', 'ember', 'simple-auth/mixins/login-controller-mixin'], function (exports, Ember, LoginControllerMixin) {

  'use strict';

  var SignInController;

  SignInController = Ember['default'].Controller.extend(LoginControllerMixin['default'], {
    actions: {
      authenticate: function authenticate() {
        var data;
        data = this.getProperties('identification', 'password');
        return this.get('session').authenticate('simple-auth-authenticator:devise', data);
      }
    }
  });

  exports['default'] = SignInController;

});